.sub-vczapi-pro-menu-admin {
  float: unset;

  li:after {
    content: '|';
    padding-left: 3px;
  }

  li:last-child:after {
    content: '';
  }
}

.vczapi-pro-admin-content-wrap {
  padding: 20px 10px;

  .vczapi-pro-admin-webhook-table {
    margin: 20px 0;
    font-size: 14px;
    text-align: left;
    border-spacing: 0 15px;

    tbody {
      th {
        padding: 10px 0;
        vertical-align: middle;
        width: 216px;
      }

      .vczapi-pro-admin-webhook-endpoint-text {
        position: relative;

        span {
          position: absolute;
          top: 20px;
          left: 20px;
        }
      }

      td {
        width: 80%;

        input {
          border: 1px solid #dededede;
          background: #fff;
          padding: 14px 45px;
          width: 100%;
        }

        a {
          text-decoration: none;
        }
      }
    }
  }
}

.vczapi-pro-admin-notice-error,
.vczapi-pro-admin-notice-success {
  padding: 10px;
  color: #fff;

  p {
    margin: 0;
  }
}

.vczapi-pro-admin-notice-error {
  background: #ad1c1c;
}

.vczapi-pro-admin-notice-success {
  background: #186317;
}

#vczapi-pro-registrants {
  .description {
    margin: 4px 0;
  }

  .dataTables_wrapper {
    max-height: 50vh;
    overflow-y: scroll;
    overflow-x: hidden;

    .dataTables_length {
      select {
        width: 50px;
        padding: 4px 8px;
      }
    }
  }

  .overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    visibility: hidden;
    opacity: 0;

    &.overlay-visible {
      visibility: visible;
      opacity: 1;
      z-index: 111111;
    }
  }

  .popup {
    margin: 70px auto;
    padding: 60px 30px;
    background: #fff;
    border-radius: 5px;
    width: 79%;
    position: relative;

    .close {
      position: absolute;
      top: 20px;
      right: 30px;
      font-size: 30px;
      font-weight: bold;
      text-decoration: none;
      color: #333;
      z-index: 1111111;
    }

    .close:hover {
      color: #06D85F;
    }
  }

  @media screen and (max-width: 700px) {
    .box {
      width: 70%;
    }
    .popup {
      width: 70%;
    }
  }
}

